//btn
%btn {
    @include transition(all ease .5s);
    @include background-gradient($color-blue-light, $color-green-light, horizontal);
    padding: 0 remy(16);
    display: inline-block;  
    cursor: pointer;
    outline: none;
    font-weight: 600;
    border-radius: remy(9999);
    font-size: remy(12);

    &:focus {
        outline: none;
        border: none;
        box-shadow: none;
        @include background-gradient($color-blue-light, $color-green-light, horizontal);
    }
    &:active {
        outline: none;
        border: none;
        @include background-gradient($color-blue-light, $color-green-light, horizontal);
    }

    @include break(480px) {
        padding: 0 2rem;
        font-size: remy(16);
    }

    @include break(768px) { padding: 0 2rem; }
    @include break(992px) {
        padding: remy(2);
        min-width: remy(120);
    }
}

.btn:not(:disabled):not(.disabled):active {
    @include background-gradient($color-blue-light, $color-green-light, horizontal);
}

.btn, input[type="submit"] {
    @extend %btn;
    color: $color-white;
    border: none;

    &:hover {
        @include background-gradient($color-blue-light, $color-green-light, horizontal);
        color: $color-white;
    }
}
.btn-transparent {
    color: $color-white;
    font-size: .7rem;
    background: none;
    text-align: center;
    @extend %btn;

    @include break(768px) { font-size: 1.0625rem; }

    &:hover {
        background: $color-white;
        color: $color-purple;
        text-decoration: none;
    }
}

.btn-small {
    @extend %btn;
    border: 2px solid $primary;
    @include font($font-size: 14, $text-transform: none !important, $color: $color-white, $font-weight: bold);
    min-width: auto;
    padding: .5rem 1rem;
    &:hover {
        background: none;
        color: $color-white;
    }
}

.btn-secondary {
    @extend .btn-small;
    padding: 0 1rem;
    font-size: remy(12);
    border-width: 2px;
    font-weight: normal;
    &:hover {
        border-color: $primary;
    }
}

.btn-white {
    @extend %btn;
    background: $color-white;
    color: $color-dark-grey;
    border: 2px solid $color-white;
    padding: remy(15);
    &:hover {
        background: none;
        color: $color-white;
    }
}


.other-btn {
    @extend .btn;    
    @include border-radius(0 8px 8px 0);    
    background: path('job_detail_btn_icon2.png') no-repeat center right, linear-gradient(to right, $color-green-light, $color-blue-light);
    transition: all ease .5s;
    position: relative;     
    padding-top: remy(8);
    padding-bottom: remy(8);
    padding-left: remy(57);    
    padding-right: remy(30) !important; 
    margin-left: remy(-32);
    &:hover {
        background: path('job_detail_btn_icon2.png') no-repeat center right, linear-gradient(to right, $color-green-light, $color-blue-light) !important;
    }

    @include break(768px) {
        padding-left: remy(116);
        margin-left: remy(-70);
    }
}
