.our-staff {    
    margin: auto;   
    padding-bottom: remy(27);
    margin-top: remy(-30);

    @include break(576px) { margin-top: -1.1875rem; }
    @include break(768px) { margin-top: remy(-202); }
    @include break(992px) { margin-top: remy(-269); }
    @include break(1200px) {
        width: remy(800);
        margin-top: remy(-265);
    }

    h2 {
        text-align: center;
        @include break(1200px) { text-align: left; }
    }

    #ourStaff {
        width: 100%;
        @include break(768px) {
            margin-top: remy(48);
            display: flex;
            flex-wrap: wrap;
        }
        @include break(992px) { margin-top: remy(33); }     
    }

    &-block {
        @include border-radius(5px);
        background: $color-white;
        box-shadow: 0 0 50px rgba($color-black, .2);
        padding: 0 remy(23) remy(3);
        text-align: center;
        position: relative;
        max-width: remy(230);
        margin: auto;
        margin-top: remy(50);
        margin-bottom: remy(40);

        @include break(576px) { margin-top: remy(70); }

        @include break(768px) { margin-top: remy(40); }

        @include break(1200px) {
            margin-top: remy(82);
            margin-bottom: 0;
        }
        
        img {
            position: relative;
            top: remy(-30);
        }
        h5 {
            font-size: remy(20);
            position: relative;
            top: remy(-8);
            margin-bottom: 0;
        }
        strong {
            font-weight: normal;
            font-size: remy(16);
            margin-bottom: remy(10);
            display: block;
        }
        p {
            font-size: remy(14);
            line-height: remy(18);
            text-align: justify;
            margin-bottom: 0;
        }
    }

    .owl-carousel {
        .owl-nav {
            [class*=owl-] {
                @include position(absolute, $top: 50%);
                @include transform(translateY(-50%));
                text-indent: -9999px;
                height: remy(28);
                width: remy(15);
            }
            .owl-next {
                background: path('news-slider-next.png') no-repeat !important;
                right: remy(30);
                @include break(480px) { right: remy(70); }
                @include break(992px) { right: remy(-60); }
            }

            .owl-prev {
                background: path('news-slider-prev.png') no-repeat !important;
                left: remy(30);
                @include break(480px) { left: remy(70); }
                @include break(992px) { left: remy(-60); }
            }
        }
    }
}