.navbar {
    @include transition($fade);
    @include position(fixed, $top: 0, $left: 0);
    width: 100%;
    z-index: 999;    
    background: $color-white !important;
    padding-left: remy(30);
    padding-right: remy(30);
    padding-bottom: remy(10);
    @include break(768px) {
        padding-bottom: remy(18);
    }



    .navbar-toggler {
        padding-left: 0;
        padding-right: 0;
        border: none;
        .navbar-toggler-icon {
            background-image: url("data:image/svg+xml;charset=utf8,%3Csvg viewBox='0 0 32 32' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath stroke='rgba(44,161,211, 1)' stroke-width='2' stroke-linecap='round' stroke-miterlimit='10' d='M4 8h24M4 16h24M4 24h24'/%3E%3C/svg%3E");
        }
    }

    .navbar-nav {
        @include break(768px) {
            -webkit-box-orient: horizontal;
            -webkit-box-direction: normal;
            -ms-flex-direction: row;
            flex-direction: row;
        }

        &#footer-menu {
            display: none;
            @include break(992px) { display: block; }
        }
    }

    &.footer-navbar { padding: .5rem 0; }

    &.bg-dark { background: none !important; }
    
    .navbar-brand {
        @include flex-grow(0);
        @include transition($fade);
        img {
            @include transition($fade);
            width: remy(120);

            @include break(480px) {
                width: auto;
            }

            @include break(768px) {
                margin-top: 0;
                margin-bottom: 0;
            }
        }
    }

    .navbar-collapse {
        @include flex-grow(1);
        margin-top: remy(8);
        justify-content: flex-end;
    }


    ul.navbar-nav li {      
        text-align: center;
        padding: remy(5) 0;     
        @include break(768px) {
            display: inline-block;
            text-align: left;
            padding: 0;
            margin-right: remy(8);
            &:last-child { margin-right: 0; }
        }

        a {
            @include font(15, $color: $color-grey !important, $font-family: $font-semi-bold);
            text-decoration: none;
            padding: 0;

            @include transition(all ease .5s);
            @include break(768px) {
                color: $color-blue-darkest !important;
            }
            
            @include break(992px) { font-size: 14px; }

            &:hover { color: $color-blue-light !important; }
        }

        &.btn {
            background: none;

            a {
                @include break(768px) {
                    padding: 0;
                    transition: none;
                    color: $color-blue-darkest !important;
                    &:hover { color: $color-white !important; }
                }
            }

            @include break(768px) {
                border: 1px solid $color-blue-light;
                border-radius: remy(20);
                margin-left: remy(9);
                padding: 0 remy(13) !important;
                min-width: auto;
                background: none;

                &:hover {
                    @include background-gradient($color-blue-light, $color-blue-dark, horizontal);
                    a {
                        color: $color-white !important;
                    }
                }
            }
        }

        .dropdown-menu {
            a.nav-link:hover, li.menu-item-has-children:hover {
                background: $primary !important;
                color: $color-white !important;
            }
        } 

        &.dropdown {
            @include transition(all ease .5s);
            ul.dropdown-menu {
                @include transition(all ease .5s);
                min-width: 8rem;
                li {
                    display: block;
                    position: relative;
                }
            }
            &:hover ul.dropdown-menu {
                @include transition(all ease .5s);
                @include break(768px) { display: block; }               
                margin: 0;
                li {
                    ul.dropdown-menu { display: none; }
                    &:hover ul.dropdown-menu {                      
                        @include break(768px) {
                            display: block;
                            @include position(absolute, $top: 0, $left: 100%);
                        }
                    }
                    &:last-child { margin-right: .4rem; }
                } 

            }
        }
    }

    .navbar-toggler { outline: none; }

    &.nav-active {
        &::before {
            @include break(0, 768px) {
                content: '';
                width: 100%;
                height: 1px;
                background: $color-light-grey;
                @include position(absolute, $top: 4.375rem, $left: 0);
            }
        }
        background: $color-white !important;
        @include break(768px) { @include transition(all ease .5s); }
        .navbar-brand img {     
            -webkit-filter: invert(1);
            filter: invert(1);
        }       
        
        ul li {
            a {
                color: $color-dark-grey !important;
            }
            &.btn {
                background: $primary;
                @include break(0px, 767px) {
                    border-radius: 0;
                }

                a { color: $color-white !important; }
            }
            ul.dropdown-menu {
                padding: 0;
                @include break(768px) { padding-top: remy(20); }
                border: none;
                li {
                    padding-left: remy(10);
                }
            }
        }
    }

    &.footer-navbar li a {
        color: $color-grey !important;

        @include break(992px, 1200px) {
            @include font(10.4);
        }       
    }
    &.footer-navbar li {
        @include break(992px, 1200px) { margin: 0 .2rem; }
    }
    
    &.sticky {
        @include position(fixed, $top: 0, $left: 0);
        // @include transition($fade);
        // background: $color-white !important;
        background: rgba($color-white, 0.7) !important;
        // padding-top: remy(5);
        // padding-bottom: remy(10);

        // .navbar-brand {
        //     img {
        //         width: remy(140);
        //     }
        // }
    }
}

.logged-in {
    .navbar { top: remy(32); }
    .sticky { @include break(768px) { top: remy(32); } }
}