.gift-block {
    @include position(fixed, $top: 28.95rem, $right: 0);
    @include transition($fade);
    overflow: hidden;
    z-index: 3;
    height: remy(55);
    width: remy(55);

    @include break(480px) { top: 40.875rem; }

    @include break(768px) { top: 34.2rem; }

    @include break(992px) {
        top: 38.3rem;
        height: remy(90);
        width: remy(90);
    }    

    .gift-icon {
        width: remy(25);
        height: remy(26);
        padding: remy(30);
        border: 2px solid $color-white;
        background: path('icon-01-small.png') center center no-repeat;
        border-radius: 100%;
        cursor: default;

        @include break(992px) {
            width: remy(42);
            height: remy(42);
            padding: remy(50);
            background: path('icon-01.png') center center no-repeat;
        }
    }
    .newslogo--active & {
        top: 50%;
        @include transform(translateY(-50%));
        height: auto;
        //top: remy(500);
        width: auto;        
        .gift-icon {
            border: none;
            border-radius: 0;
            background: path('gift-icon.png') no-repeat;
            background-size: 100%;
            width: 3.9rem;
            height: 3.6rem;
            padding: 0;
            cursor: pointer;
            
            @include break(768px) {
                width: remy(95);
                height: remy(65);
            }
        }
    }
}

.popup-overlay {
    @include position(fixed, 0, 0, 0, 0);
    transition: all 0.4s ease;
    width: 100%;
    height: 100%;
    z-index: -1;
    opacity: 0;

    &.active {
        display: block;
        transition: all ease .4s;        
        opacity: 1;
        z-index: 999;
        background: rgba($color-black, 0.8);
        @include break(768px) {
            background: rgba($color-black, 0.6);
        }
    }   
}

.popup-form {
    // background: rgba($color-black, 0.6);
    display: none;
    z-index: 9999;
    position: fixed;    
    top: 0px;
    right: 0px;     
    height: 100%;

    @include break(768px) { width: 37.5rem; }
    @include break(992px) { width: remy(785); }

    @include break(0, 1024px) { overflow-y: scroll; }
    
    @include break(1200px) {
        // @include position(fixed, 0, 0, 0, 0);
        // width: 100%;
        // height: 100%;
        z-index: 999;
    }
    .popup-block {
        @include position(relative, $top: 6rem, $right: remy(-280));       
        transition: all 0.3s ease;
        background: $color-white;
        // padding: remy(30) remy(35);
        padding: 0.875rem 1.1875rem;
        border-radius: remy(5) 0 0 0;
        width: remy(250);
        z-index: 999;
        float: right;
        @include break(480px) {
            width: remy(385);
            padding: remy(15) remy(20);
        }
        @include break(576px) {
            padding: remy(50) remy(50);
        }

        @include break(768px) {
            width: remy(550);
        }

        @include break(992px) {
            @include transform(translateY(-50%));
            top: 50%;           
            width: remy(715);
            padding: remy(50) remy(70);
            right: remy(-785);

        }

        &:before {          
            @include position(absolute, $top: 0, $left: remy(-35));
            content: '';
            border-style: solid;            
            border-color: transparent transparent $color-white transparent;
            border-width: 0 0 remy(758) remy(35);
            height: 100%;
            .mozzilla & { border-width: 0 0 remy(745) remy(35); }
            @include break(480px) {
                border-width: 0 0 remy(924) remy(50);
                left: remy(-50);
                .mozzilla & { border-width: 0 0 remy(873) remy(50); }
            }
            @include break(768px) {
                border-width: 0 0 remy(633) remy(50);
                .mozzilla & { border-width: 0 0 remy(626) remy(50); }
            }
            @include break(992px) {
                // border-width: 0 0 remy(602) remy(70);
                border-width: 0 0 39rem remy(70);
                left: remy(-70);
                .mozzilla & { border-width: 0 0 remy(602) remy(70); }
            }
            @include break(1200px) {
                .mozzilla & { border-width: 0 0 remy(618) remy(70); }
            }
        }

        h3 {
            line-height: remy(22);
            margin-bottom: remy(10);
            font-size: remy(17);
            @include break(480px) {
                font-size: remy(20);
                line-height: remy(25);
                margin-bottom: remy(10);
            }
            @include break(576px) {
                font-size: remy(24);
                line-height: remy(33);
                margin-bottom: remy(20);
            }
        }

        p {
            @include font(12, 16);
            margin-bottom: 0;
            @include break(480px) { @include font(15); }
        }
        strong { font-weight: 600; }

        .contact-info {
            .info-block {
                font-size: 15px;
            }
        }

        .form-block {
            padding: 0;
            background: none;            
            margin-top: 0.875rem;
            @include break(480px) { margin-top: remy(20); }
            @include break(576px) { margin-top: remy(30); }
            @include break(992px) { padding-right: remy(45); }

            h3 {
                color: $color-main;
                margin-bottom: 0.875rem;
                @include break(480px) { margin-bottom: remy(20); }
                @include break(576px) { margin-bottom: remy(30); }
            }
            label { color: $color-main; }

            .form-control {
                border-bottom: 2px solid $color-blue-darkest;
                color: $color-main;
                @include break(0, 479px) { margin-bottom: 0.25rem; }                
                @include break(480px, 575px) { margin-bottom: 1rem; }
            }
            textarea {                
                @include break(0, 575px) { height: 3.625rem }
            }
            .form-check, .btn {
                float: none;
            }
            .form-check {
                font-weight: 600;
                .helper {
                    &::before {
                        border-color: $color-grey-off;
                    }
                }
            }
            .btn { margin-top: remy(25); }
        }

        .contact-info {
            margin-top: remy(65);
            .icon-block img {
                margin-top: 0;
                width: remy(25);
            }
        }

        .close {
            @include position(absolute, $top: remy(-60), $right: remy(25));         
            background: path('close-mobile.png');
            width: remy(24);
            height: remy(24);
            @include break(768px) {
                background: path('close.png');
                top: remy(30);
                width: remy(38);
                height: remy(38);
            }
        }
    }

    &.popup-invalid {
        .popup-block {
            &:before { border-width: 0 0 44.625rem remy(70); }
        }
    }

    &.popup-sent {
        .popup-block {
            &:before { border-width: 0 0 42.625rem remy(70); }
        }
    }

    &.active {
        display: block;
        transition: all ease .3s;
        .popup-block {
            opacity: 1;
            right: 0;
            transition: all ease .3s;

        }
    }
}

.mobile-hidden { @include break(0, 991px) { display: none; } }
.popup--active {    
    width: 100%;
    @include break(0, 1024px) {
        position: fixed;
    }
}