.menu-btn{
    // padding: 10px;
    width: 50px;
    height: 50px;   
    padding: 7.5px 10px; 
    @include break(768px) {
        width: 66px;
        height: 66px; 
        padding: 6.5px 10px;      
    }

    text-align: center;
    background: #2aa4d2;
    background: -webkit-gradient(left bottom,right top,color-stop(0,#2aa4d2),color-stop(100%,#4dbab2));
    background: linear-gradient(45deg,#2aa4d2 0,#4dbab2 100%);
    position: fixed;
    z-index: 99;
    right: 15px;
    top: 50%;
    transform: translateY(-50%);
    box-shadow: 0 0 50px rgba($color-black, .2);
    cursor: pointer;
    border-top-left-radius: 5px;
    border-bottom-left-radius: 5px;
    transition: all 0.5s ease-in-out;
    
    
    img{
        display: inline-block;
        vertical-align: middle;
    }

}


