.consulting-wrap1{
    
    margin: 0 40px;
    margin-bottom: remy(70);

     @include break(768px) {
         margin: 0 80px 45px;
    }


    @include break(992px) {
         margin: 0 140px 45px;
    }

    @include break(1200px) {
        margin-left:135px;
        margin-right: 135px;
    }


    .content{

        @include break(992px){
            padding: 0 80px;
        }
        @include break(992px){
            padding: 0;
            // padding-top: 35px;
        }
        h2{
            margin-bottom: 15px;
        }
    }

    
    ul{
        list-style: none;
        padding: 0;
        margin: auto;
        width: 328px;
        margin-top: 50px;
        max-width: 100%;
        @include break(992px) {
            margin-top: 0;
        }
        @include break(1200px) {
            width: 500px;
            margin-left: auto;
            
        }

        li{
            background: #fff;
            padding: remy(15);
            text-align: center;
            color: $color-blue-darkest;
            font-size: remy(14);
            font-family: $font-semi-light;
            box-shadow: 0 0 40px rgba(0,0,0,.18);
            border-bottom: 4px solid #1bcfc3;
            border-radius: remy(5);
            position: relative;
            margin-bottom: remy(34);

            @include break(480px){
                width: remy(144);
                height: remy(85);
                display: block;
                float: left;
                margin-right: remy(34);
               
            }

            &:after{
                content: '';
                position: absolute;
                display: block;
                background: path('arrow.png') no-repeat;
                width: 30px;
                height: 30px;
            }
        }   

        li:first-child{
             @include break(480px){
                height: remy(203);
            }
            &:after{
                
                bottom: -35px;
                left: 0;
                right: 0;
                margin: auto;
                transform: rotate(90deg);

                @include break(480px) {
                    top: 28px;
                    left: auto;
                    right: -33px;
                    transform: rotate(0);
                    margin: 0;
                }
            }

        }
        li:nth-child(2){
                margin-right: 0;
            @include break(1200px) {
                margin-right: remy(34);
            }
            &:after{
                bottom: -35px;
                left: 0;
                right: 0;
                margin: auto;
                transform: rotate(90deg);
                @include break(1200px) {
                    transform: rotate(0deg);
                    top: 28px;
                    left: auto;
                    right: -33px;   
                    bottom: auto;
                }

            }
        }
        li:nth-child(3){
            margin-right: 0;    
            &:after{
                bottom: -35px;
                -webkit-transform: rotate(90deg);
                transform: rotate(90deg);
                left: 0;
                right: 0;
                margin: auto;
            }
        }       
        li:nth-child(5){
            &:after{
                display: none;
                @include break(480px) {
                    top: 28px;
                    left: -33px;
                    transform: rotate(-180deg);
                    display: block;
                }

            }
        }
        li:nth-child(4){
            &:after{
                bottom: -35px;
                -webkit-transform: rotate(90deg);
                transform: rotate(90deg);
                left: 0;
                right: 0;
                margin: auto;
                @include break(480px) {
                    display: none;
                }
            }
        }

        li:last-child{
            margin-right: 0;    
        }

    }
}

