.con-solution{
    
    border: 2px solid #1bcfc3;
    border-radius: 100px;   
    
    position: relative;
    margin-bottom: 65px;

    @include break(768px) {
        margin: 0 30px; 
        padding: 0px 10px 0;
    }

    .line-top, .line-bottom{
        display: block;
        position: relative;
        text-align: center;

        h3{
            background: #fbfbfb;
            font-size: 21px;
            display: inline-block;
            padding: 0px 15px;
            margin: 0 25px;

            @include break(768px) {
                margin: 0px 65px;
            }               
            @include break(992px) {
                margin: 0px 55px;
            }           
            @include break(1200px) {
                margin: 0px 35px;
            }           
/*          @include break(768px) {
                margin: 0px 35px;
} */
        }
        
    }
    .line-top{
        top: -17px;
            span{
                color: $color-blue-lightest;
            }
    }
    .line-bottom{
        bottom:-12px;
    }
    ul{
        list-style: none;
        padding: 0;
        display: inline-block;
        text-align: center;

        li{
            width: remy(154);
            height: remy(154);
            border-radius: 50%;
            background: #fff;
            box-shadow: 0 0 40px rgba(0, 0, 0, 0.18);
            text-align: center;
            display: inline-block;
            position: relative;
            margin: 0 remy(15);
            margin-bottom: remy(30);

            &:hover{
                background: rgba(42,164,210,1);
                background: -moz-linear-gradient(45deg, rgba(42,164,210,1) 0%, rgba(77,186,178,1) 100%);
                background: -webkit-gradient(left bottom, right top, color-stop(0%, rgba(42,164,210,1)), color-stop(100%, rgba(77,186,178,1)));
                background: -webkit-linear-gradient(45deg, rgba(42,164,210,1) 0%, rgba(77,186,178,1) 100%);
                background: -o-linear-gradient(45deg, rgba(42,164,210,1) 0%, rgba(77,186,178,1) 100%);
                background: -ms-linear-gradient(45deg, rgba(42,164,210,1) 0%, rgba(77,186,178,1) 100%);
                background: linear-gradient(45deg, rgba(42,164,210,1) 0%, rgba(77,186,178,1) 100%);
                filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#2aa4d2', endColorstr='#4dbab2', GradientType=1 );

                .inner-circle{
                    border-color: #fff;

                    span{
                        color: #fff;
                    }
                }
                .tooltp{
                    opacity: 1;
                    bottom: 80%;
                    z-index: 1;
                }
            }

            .inner-circle{
                width: remy(142);
                height: remy(142);
                border-radius: 50%;
                border: 2px solid #1bcfc3;
                margin: auto;
                position: absolute;
                top: 50%;
                left: 0;
                right: 0;
                transform: translateY(-50%);
                transition: all 0.4s ease;  

                span{
                    width: 100%;
                    height: 100%;
                    display: flex;
                    justify-content: center;
                    align-items: center;
                    padding: 0 remy(5);
                    font-size: 14px;
                    font-family: $font-semi-light;
                }

            }
            .tooltp{
                position: absolute;
                bottom: 100%; 
                background: rgba(255, 255, 255, 0.85);
                left: 0;
                right: 0;
                width: 100%;
                padding: 10px;
                box-shadow: 0 0 3px #0000007a;      
                border-radius: 4px;
                opacity:0;
                transition: all 0.4s ease;
                z-index: -1;


                .tooltp-arrow{
                    width: 50px;
                    height: 25px;
                    margin: auto;
                    position: absolute;
                    top: 100%;
                    left: 0;
                    right: 0;
                    overflow: hidden;

                    &:after{
                        content: '';
                        width: 15px;
                        height: 15px;
                        margin: auto;
                        position: absolute;
                        top: 0;
                        background: rgba(255, 255, 255, 0.85);
                        transform: translateX(-50%) translateY(-50%) rotate(45deg);
                        left: 50%;
                        box-shadow: 0 0 3px rgba(0, 0, 0, 0.4);     
                    }           
                }               



                p{
                    color: #000;
                    margin-bottom: 0;
                }
            }
        }

        li:nth-last-child(-n+3){
            @include break(1200px) {
                margin-bottom: 0;
        }

        }
    }
}

