                margin-left: 8px; 
                width: 30.4%;
                height: 191px;  

                @include break(480px) { 
                    margin-left: 22px;
                    width: 21.4%;
                    
                }
                @include break(580px) { 
                    
                    font-size: 25px; 
                    margin-left: 42px;
                }  
            }
            li:first-child{
                margin-left: 0;
            }
        }
    }



    .customer-needs{
        margin-bottom: 30px;
        @include break(992px) {
            margin-bottom: 0;
        }

        .content{
            margin-bottom: 30px;
            margin-top: 0;

            @include break(992px) {
                margin-top: 50px;
            }
        }
        img{
            display: block;
            margin: auto;
        }
    }

}

.q-icon-list{
    margin-top: 50px;
    margin-bottom: remy(50);
    @include break(992px) {
        margin-top: 10px;
        margin-bottom: remy(70);        
    }  


    ul{
        padding: 0;
        list-style: none;
        width: 100%;
        display: flex;
        flex-wrap: wrap;
        justify-content: center;
        @include break(1200px) {
            justify-content: normal;
        }

        li{

            display: inline-block;            
            width: 50%;
            margin-bottom: 20px;
            &:last-child { margin-bottom: 0; }

            @include break(580px) { width: 36%; }
            @include break(768px) {
                width: 33.33%;
                margin-bottom: 15px;
            }   
            @include break(992px) { width: 20%; }
            @include break(1200px) { width: 16.66%; }
            .q-circle{
                width: 112px;
                height: 112px;
                border-radius: 50%;
                box-shadow: 0 0 20px rgba(0,0,0,.18); 
                background: #fff;  
                position: relative;
                margin: auto;

                &:before{
                    content: '';
                    position: absolute;
                    top: 0;
                    width: 102px;
                    height: 102px;
                    background: -webkit-gradient(left bottom,right top,color-stop(0,#2aa4d2),color-stop(100%,#4dbab2));
                    background: linear-gradient(45deg,#2aa4d2 0,#4dbab2 100%);
                    display: block;
                    border-radius: 50%;
                    z-index: 1;
                    -webkit-transform: translateY(-50%);
                    transform: translateY(-50%);
                    top: 50%;
                    left: 0;
                    right: 0;
                    margin: auto;
                }

                .q-inner-circle{
                    width: 98px;
                    height: 98px;
                    border-radius: 50%;
                    background: #fff;
                    position: absolute;
                    top: 50%;
                    left: 0;
                    right: 0;
                    transform: translateY(-50%);
                    display: block;
                    margin: auto; 
                    text-align: center;
                    line-height: 90px;
                    z-index: 2;
                    
                }
            }

            .sub-title{
                font-size: 14px;
                font-family: $font-semi-bold;
                text-align: center;
                color: #3d545c;
                margin-top: 20px;
                @include break(768px) { margin-top: 30px; }
            }
        }
    } 
}

