.jobs {
    p {
        font-size: 1rem;
        line-height: remy(24);
    }
}

.open-positions {   
    position: relative;
    @include break(480px) { padding-top: remy(10); }
    @include break(768px) { padding-top: 1rem; }

    h5 {
        font-size: remy(18);
        color: $color-main !important;
        font-family: $font-semi-light;
        @include break(480px) { font-size: remy(22); }
    }

    ul {
        padding: 0;
        list-style: none;       
        margin-top: remy(20);

        @include break(480px) {
            margin-top: remy(27);
            margin-bottom: remy(25);
        }
        
        @include break(768px) {         
            margin-bottom: remy(43);
        }

        li {
            @include border-radius(remy(10));
            padding: remy(8);
            border: 2px solid $color-white-off;         
            margin-bottom: remy(10);

            @include break(480px) { padding: remy(20); }
        }       
    }
    .job-title {
        width: 100%;        
        display: inline-block;
        @include break(1200px) {
            width: 33%;
        }

        a {
            font-size: 1rem;
            color: $color-blue !important;
            &:hover { color: $color-blue-darkest !important; }
            @include break(480px) {
                margin-bottom: remy(10);
                font-size: remy(20);
            }           
            @include break(768px) {
                font-size: remy(22);
                margin-bottom: remy(15);
            }
            @include break(1200px) { margin-bottom: 0; }
        }       
    }
    .job-address {
        width: 55%;
        display: inline-block;      
        font-family: $font-semi-light;

        @include break(480px) {
            width: 64%;
            margin-left: remy(15);
            font-size: 1rem;
        }
        @include break(768px) {
            margin-left: remy(22);
        }
        @include break(1200px) {            
            width: 33%;
            text-align: center;
            margin-left: 0;
        }

        .fa {
            color: $color-blue-dark;            
            margin-right: remy(5);
            @include break(768px) { font-size: remy(20); }
        }
    }
    .job-read-more {
        width: 43%;
        display: inline-block;
        text-align: right;      
        font-family: $font-semi-light;

        @include break(480px) {
            width: 30%;
            font-size: 1rem;
        }

        @include break(1200px) {            
            width: 33%;
        }

        a {
            color: $color-main;
            &:hover { color: $color-blue; }
        }
    }
}

.job-detail {
    h1 { margin-bottom: remy(8) !important; }
    p {
        font-size: 1rem;
        line-height: remy(24);
    }
    .job-caption {
        padding: 0;
        list-style: none;
        margin-bottom: remy(20);

        @include break(576px) { margin-bottom: remy(30); }
        
        li {
            display: inline-block;
            margin-left: remy(10);
            position: relative;
            border-left: 1px solid $color-green-light;
            padding-left: remy(10);
            font-size: remy(16);
            color: $color-green-light;
            font-family: $font-semi-light;
            &:first-child {
                margin-left: 0;
                border-left: none;          
                padding-left: 0;
            }
            &.job-type { font-weight: bold; }
            a {
                color: $color-green-light;
                &:hover { color: $color-blue; }
            }
        }
    }
    .dscp {
        ul {
            padding: 0;
            list-style: none;
            margin-bottom: remy(15);
            li {
                background: path('bullet.png') 0 5px no-repeat;
                font-family: $font-semi-light;
                padding-left: remy(20);
                font-size: 1rem;
                line-height: remy(24);
            }
        }
    }

    .detail-btn {
        @include border-radius(0 8px 8px 0);
        transition: all ease .5s;
        position: relative;     
        padding: remy(8) remy(15);      
        padding-left: remy(57);     
        margin-left: remy(-32);     
        &:hover {
            @include background-gradient($color-green-light, $color-blue-light, horizontal);
        }

        @include break(768px) {
            padding-left: remy(116);
            margin-left: remy(-70);
        }

        i {
            width: remy(9);
            height: remy(15);
            background: path('job_detail_btn_icon.png') no-repeat;
            display: inline-block;
            margin-left: remy(10);
            float: left;
            @include break(480px) { margin-top: remy(4); }
        }
        strong {
            display: inline-block;
            float: left;

        }
    }

    input[type="submit"] {
        &.btn {
            background: path('job_detail_btn_icon2.png') no-repeat center right, linear-gradient(to right, $color-green-light, $color-blue-light);
            padding-right: remy(30);
            margin-top: remy(20);
            margin-bottom: remy(15);
        }
    }
}

.job-application {
    h4 {
        font-size: remy(20);
        margin-bottom: remy(20);
        @include break(480px) {
            font-size: remy(30);
        }
        @include break(576px) {         
            margin-bottom: remy(40);
        }       
    }

    .form-group {
        margin-bottom: remy(5);
        @include break(480px) {
            margin-bottom: 1rem;
        }       
    }
    
    .form-control {
        background: $color-bg-input;
        border-radius: 0;
        border: none;       
        width: 89%;
        padding: remy(4) remy(9);
        @include break(768px) { width: 100%; }
        @include break(992px) { width: 91%; }
        @include break(1200px) { width: 89%; }
        &:focus {
            box-shadow: 0 0 0 2px $color-blue-light;
            background: $color-white;
        }
    }
    textarea.form-control {     
        background: path('textarea-bg.png') no-repeat bottom right $color-bg-input;
        resize: none;
        width: 88.8%;
        @include break(768px) {
            width: 100%;
            height: remy(90);
        }
        @include break(992px) {
            width: 95.8%;
        }
    }

    .btn {
        min-width: auto;
        padding: .3rem remy(22);        
        margin-top: remy(22);       
        font-size: remy(12);
        position: relative;
        @include break(576px) {         
            font-size: remy(14);
            margin-top: remy(15);
        }
        @include break(768px) {         
            margin-top: remy(42);
            margin-bottom: remy(25);
            margin-left: 0;
        }
        @include break(992px) { margin-left: remy(-30); }
        @include break(1200px) { margin-left: remy(132); }
    }
}

.upload-btn {
    position: relative; 
    .browse {
        @include background-gradient($color-blue-light, $color-blue-dark, horizontal);
        @include position(absolute, $top: 0, $right: remy(48));
        color: $color-white;
        font-weight: normal;
        padding: remy(7) remy(20);
        @include break(480px) { right: remy(57); }
        @include break(576px) { right: remy(62); }
        @include break(768px) { right: remy(15); }
        @include break(992px) { right: remy(46); }
        @include break(1200px) { right: remy(48); }
    }
    input[type="file"] {
        opacity: 0;
        position: relative;
        z-index: 1;
    }
    input[type="text"] {
        @include position(absolute, $top: 0, $left: 15);
        width: 78.5%;
        @include break(768px) {
            width: 80%;
        }
    }
}