html {
    font-size: 100%;
    overflow-x: hidden;
}

body {
    font-size: 1rem;
    font-family: $font-main;
    line-height: 1.125rem;
    @include break(0, 1199px) {
        overflow-x: hidden !important;
        width: 100%;
    }
}

article, aside, dialog, figcaption, figure, footer, header, hgroup, main, nav, section { z-index: 0; }

* {
    margin: 0;  
    box-sizing: border-box;
    overflow-wrap: break-word;
}

p { @include font; }

ul, ol {
    margin: 0;
    @include font;
}

a {
    @include transition($fade);
    text-decoration: none;
    color: $color-link;
    &:hover {
        color: $color-black;
        text-decoration: none;
    }
}

h1, .h1 {
    color: $primary;
    font-size: remy(24);
    font-family: $font-semi-light;
    letter-spacing: 1px;    
    &.after {
        padding-bottom: 1.125rem;
        &::after {
            @include position(absolute, $bottom: 0, $left: 50%);
            @include transform(translate(-50%));
            content: '';
            width: 7.375rem;
            height: .1875rem;
            background: $color-blue;
        }
    }
    @include break(480px) {
        font-size: 2.5rem;
    }
}

h2, .h2 {
    position: relative;
    @include font(26, 35, $font-family: $font-semi-light);
    @include break(480px) { @include font(44, 54); }
    @include break(992px) {
        line-height: remy(40);
    }
    &.after {
        padding-bottom: remy(15);
        @include break(992px) { padding-bottom: 1.125rem; }
        &::after {
            @include position(absolute, $bottom: 0, $left: 50%);
            @include transform(translate(-50%));
            content: '';
            height: .1875rem;
            background: $color-blue-light;
            width: remy(80);
            @include break(992px) { width: 7.375rem; }
        }
    }
    span {
        display: block;
        margin-bottom: remy(10);
        @include font(25, 25, 700, $color-blue-light);
    }
}


h3, .h3 {
    @include font(20, 20, $font-family: $font-semi-light);
    @include break(480px) {
        @include font(26, 26, $font-family: $font-semi-light);
    }
}

h4, .h4 {
    @include font(24, 24, $font-family: $font-semi-light);
    @include break(576px, 992px) {
        font-size: 1.3rem;
    }
}

h5, .h5 {
    position: relative;
    @include font(22, 22, bold, $font-family: $font-semi-light);
    &.after {
        padding-bottom: 0.8rem;
        &::after {
            @include position(absolute, $bottom: 0, $left: 0);
            content: '';
            width: 4.375rem;
            height: .125rem;
            background: $color-blue;
        }
    }
}

h6, .h6 {
    position: relative;
    @include font(14, 20, $font-family: $font-heading);
    @include break(480px, 767px) {
        font-size: remy(18);
        line-height: remy(24);
    }
}

blockquote {
    @include font;
    margin: 0.25rem 0;
    padding: 0.25rem 3rem;
    line-height: 1.45;
    position: relative;
    color: $color-grey;
    font-style: italic;
    &::before {
        display: block;
        content: "\201C";
        font-size: 5rem;
        @include position(absolute, $left: 0px, $top: -20px);
        color: $color-grey;
    }
    cite {
        color: $color-light-grey;
        font-size: 14px;
        display: block;
        margin-top: 5px;
        &::before {
            content: "\2014 \2009";
        }
    }
}

address {
    font-size: remy(14);

    @include break(480px, 767px) {
        font-size: remy(16);
    }
}

table {
    width: 100%;
    margin-bottom: remy(15);
    tr {
        border-bottom: 1px solid $color-light-grey;
        td, th { padding: remy(5); }
    }
}

dl {
    dd {
        margin-left: remy(15);
    }
}

figcaption { @include font; }

.pagination {
    margin-top: remy(40);
    margin-left: remy(11);
    display: block;
    text-align: center;

    li { display: inline-block; }
}

.error404, .search {
    #searchform {
        label {
            font-size: 1.375rem;
            margin-bottom: 1rem;
            margin-top: 1rem;
            @include break(768px) {
                margin-top: 2rem;
                margin-bottom: 1.6875rem;
            }
        }
        .form-control {
            &:focus { box-shadow: none; }
        }
    }
    .txt-container {
        padding-bottom: remy(35) !important;
        @include break(480px) { padding-bottom: remy(39) !important; }
        @include break(768px) { padding-bottom: remy(70) !important; }
    }
}

.container {
    @include break(1200px) {
        max-width: 1190px;
    }
}

.wrapper { padding: 0; }

.pad-100 {
    padding-top: remy(100);
    padding-bottom: remy(100);
}
.pad-80 {
    padding-top: 2rem;
    padding-bottom: 2rem;
    @include break(992px) {
        padding-top: 3rem;
        padding-bottom: 3rem;
    }
    @include break(992px) {
        padding-top: remy(73);
        padding-bottom: remy(80);
    }
}

.pad-40 {
    padding-top: 2.5rem;
    padding-bottom: 2.5rem;
}

.mobile-elem {
    display: block;
    @include break(768px) { display: none; }
}
.desktop-elem {
    display: none;
    @include break(768px) { display: block; }
}

.overlay {
    width: 100%;
    height: 100%;
    @include position(absolute, $top: 0, $left: 0);
    @include transition($fade);
}

.logged-in #page {
    position: relative;
}

.container-fluid {
    &.front-page, &.not-home {
        position: relative;
        top: remy(71.83);
        @include break(480px) { top: remy(89); }
        @include break(768px) { top: remy(97); }
    }
}

.container-foo {    
    &.not-home {
        padding-top: remy(71.83);
        @include break(480px) { padding-top: remy(89); }
        @include break(768px) { padding-top: remy(77); }
    }    
}

.firstpage-footer {
    margin-top: 6.48937rem;
    @include break(480px) { margin-top: 8.5625rem; }
    @include break(768px) { margin-top: 8.5rem; }
    @include break(992px) { margin-top: remy(100); }
    @include break(1200px) { margin-top: remy(77); }
}

.about-footer {
    position: static !important;
    margin-top: remy(-40);
    @include break(768px) { margin-top: remy(40); }
    @include break(992px) { margin-top: 0; }
}
.aboutpage-footer {
    position: static;
    margin-top: remy(40);
    @include break(480px) { margin-top: 3.5rem; }
    @include break(576px) { margin-top: 4.5rem; }
    @include break(768px) { margin-top: -70px; }
    @include break(992px) { margin-top: -200px; }
    @include break(1200px) { margin-top: -185px; }
    .container-foo.not-home {
        padding-top: 0;
        @include break(1200px) { padding-top: 4.8125rem; }
    }
}

.container-inner {  
    width: 100%;    
    max-width: remy(1035);
    margin: auto;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-wrap: wrap;
    flex-wrap: wrap;
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
    -webkit-box-pack: justify;
    -ms-flex-pack: justify;
    justify-content: space-between;
}

.form-check {
    position: relative;
    display: block;
    padding-left: remy(7);

    @include break(480px) {
        padding-left: 1.25rem;
    }

    &-label {
        @include font(11, 11);
        margin-left: remy(5);
        cursor: pointer;
        vertical-align: sub;

        @include break(480px) {
            @include font(17, 24);
        }
    }
    &-input {
        @include position(absolute, $top: 0, $left: 0);
        width: auto;
        opacity: 0.00000001;                
        margin-left: 0;
        margin-top: remy(9);
        @include break(480px) {
            margin-left: remy(-20);
            margin-top: .5rem;
        }
    }

    .helper {
        @include position(absolute, $top: 47%, $left: remy(-4));        
        cursor: pointer;
        display: block;
        font-size: 1rem;
        user-select: none;
        color: #e7e7e7;
        @include break(0, 479px) {
            @include transform(translateY(-47%));           
        }
        @include break(480px) {
            top: 0;
        }
        &::before {
            @include position(absolute, $top: 0, $left: 0);
            content: '';            
            transition: transform 0.28s ease;
            border-radius: 3px;
            border: 1px solid $color-grey-off;
            width: remy(11);
            height: remy(11);

            @include break(480px) {
                margin: remy(4);
                height: remy(17);
                width: remy(17);                
            }
        }
        &::after {
            @include position(absolute, $top: remy(3), $left: remy(3));
            content: '';
            display: block;
            height: remy(5);
            width: remy(5);

            @include break(480px) {
                width: remy(7);
                height: remy(7);
                top: remy(9);
                left: remy(9);
            }
        }
    }
    input[type="checkbox"]:checked ~ .helper::after {
        background: $color-blue;
    }
}

.question-form {
    &.not-home {
        display: none;
    }

    .mc4wp-success {
        p {
            @include position(absolute, $top: 50%);
            @include transform(translate(0, -50%));
        }
    }

    @include position(absolute, $top: 19.5rem, $right: 0);
    @include transition($fade);
    background: $color-grey-lightest;
    padding: remy(15);
    z-index: 2;
    width: 70%;
    border-radius: remy(5) 0 0 0;
    min-height: 188px;

    .newsletter-success & { top: 24rem; }

    @include break(480px) {
        top: 28rem;
        padding: remy(25) remy(20);
        .newsletter-success & { top: 34rem; }
    }

    @include break(576px) {
        width: remy(380);
        .newsletter-success & { top: 34rem; }
    }
    
    @include break(768px) {
        top: 19.875rem;
        padding: remy(30);
        .newsletter-success & { top: 26rem; }
    }

    @include break(992px) {
        top: remy(400);
        .newsletter-success & { top: 31rem; }
    }
    
    &:before {      
        @include position(absolute, 0, $left: remy(-30));
        border-style: solid;
        content: '';
        border-color: transparent transparent $color-grey-lightest transparent;
        border-width: 0 0 12.625rem remy(30);
        height: 100%;
        .mozzilla & { border-width: 0 0 12.688rem remy(30); }
        .newsletter-success & { border-width: 0 0 remy(120) remy(30); }

        @include break(480px) {
            border-width: 0 0 17.125rem remy(30);
            .mozzilla & { border-width: 0 0 19.125rem remy(30); }
            .newsletter-success & { border-width: 0 0 remy(178) remy(30); }
        }
        @include break(576px) {
            .mozzilla & { border-width: 0 0 17.125rem remy(30); }
            .newsletter-success & { border-width: 0 0 remy(152) remy(30); }
        }
        @include break(768px) {
            border-width: 0 0 18.5rem remy(30);
            .mozzilla & { border-width: 0 0 18.588rem remy(30); }
            .newsletter-success & { border-width: 0 0 11.5rem remy(30); }
        }
    }

    .form-check {        
        @include break(768px) {
            margin-bottom: remy(5);            
        }
    }

    .form-control {
        background: none;
        border: none;
        border-bottom: 2px solid $color-green-light;
        border-radius: 0;
        padding-left: 0;
        padding-right: 0;
        &:focus {
            box-shadow: none;
        }
    }

    h3 {
        @include font(18, 18, $font-family: $font-semi-light);        
        margin-bottom: remy(8);

        @include break(480px) {
            font-size: remy(26);
            line-height: remy(26);
            margin-bottom: remy(20);
        }
    }
    p {
        font-size: remy(11);
        margin-bottom: 0;
        .newsletter-success & { font-size: 1rem; }
        @include break(480px) {
            font-size: remy(17);
            margin-bottom: 1rem;
        }
    }
    
    .btn {
        border: 1px solid $color-blue-dark;
        margin-top: remy(15);
        padding: 0 remy(27);
        margin-left: remy(52);
        font-size: remy(12);
        @include break(480px) {
            font-size: remy(16);
            padding: remy(2) remy(40);
            margin-top: remy(22);
        }
        @include break(992px) {
            margin-left: remy(83);
            padding: remy(2);
        }
        &:hover {
            background: white;
            border: 1px solid $color-blue-dark;
            color: $color-blue-dark;            
        }       
    }       
}

.newslogo--active {
    .question-form {
        top: 19rem;
        @include break(480px) { top: 27.375rem; }
        @include break(768px) { top: 20rem; }
        @include break(992px) { top: 24.875rem; }
    }
}

.overlap-block, .overlay-block {
    position: relative;
    //overflow: hidden;
    
    @include background-gradient($color-white-off, $color-white-off2, vertical);

    @include break(992px) {
        padding-bottom: remy(85);
        .no-pad & { padding-bottom: 0; }
    }
}


//LAST-NEWS
.last-news {
    padding-top: 0;
    position: relative;
    padding-bottom: remy(50);
    margin-top: remy(-97);

    @include break(480px) {
        margin-top: remy(-135);
        padding-bottom: remy(80);
    }

    @include break(768px) {
        margin-top: remy(-50);
    }

    @include break(992px) {
        padding-top: remy(440);
        padding-bottom: remy(130);
        margin-top: remy(-470);
    }

    @include break(1200px) {
        margin-top: remy(-510);
    }

    &::after {
        content: '';
        clear: both;
        display: block;
    }
    .show-all {
        position: relative;
        width: 100%;
        text-align: center;     
        @include break(992px) { 
            margin-top: remy(-30);
            width: remy(223);
            margin-left: 3.5rem;
            margin-right: remy(57);
            float: left;
        }
        @include break(1200px) {
            margin-left: remy(125);
            margin-right: 7rem;
        }
        figure {
            img {
                width: remy(132);
                @include break(480px) {
                    width: auto;
                }
            }
            figcaption {
                @include font(28, 28, $font-family: $font-semi-light);              
                margin-bottom: remy(18);
                margin-top: remy(-37);
                @include break(480px) {
                    margin-bottom: remy(20);
                    margin-top: -3.1875rem;
                    @include font(40, 40, $font-family: $font-semi-light);
                }
            }
        }
        .btn {
            border: 1px solid $color-blue-dark;
            margin-top: remy(10);
            &:hover {
                background: $color-white;
                color: $color-blue-dark;
            }
        }
    }
}


.last-news-block {
    width: 100%;
    margin-top: remy(30);
    position: relative; 

    h2 { .not-home & { text-align: center; } }

    &.about-news { margin-top: remy(-95); }

    @include break(480px) {
        margin-top: remy(50);
        &.about-news {
            margin: auto;
            margin-top: remy(-150);
        }
        .not-home & { width: 80%; }
    }

    @include break(576px) {
        &.about-news { margin-top: remy(-180); }
    }
    @include break(768px) {
        &.about-news { margin-top: -5rem; }
        .front-page & {
            width: 33rem;
            margin: auto;
            margin-top: remy(50);
        }
    }

    @include break(992px) {
        margin-top: remy(25);
        .not-home & { width: 70%; }
        .front-page & {
            float: left;
            margin-top: remy(25);
        }
    }

    @include break(1200px) {
        width: remy(700);
        .not-home & {
            width: 53rem;
            margin: auto;
            margin-top: remy(20);
        }
        .news-detail & { margin-top: remy(-70); }
        .about--active & {
            margin-top: remy(-28);
        }
    }

    article {
        @include background-gradient($color-blue-light, $color-blue-dark, horizontal);
        width: 75%;
        border-radius: remy(7);
        box-shadow: 0 0 50px rgba($color-black, .2);
        margin: auto;
        margin-top: remy(45);
        position: relative;
        overflow: hidden;
        &:first-child { margin-top: 0; }

        &:hover {
            .overlay {
                position: absolute;
                top: 0;
                left: 0;
                width: 100%;
                height: 100%;
                background: rgba($color-black, 0.2);
                z-index: 3;
            }
        }

        .not-home & {
            box-shadow: 0 0 30px rgba($color-black, .2);
        }

        @include break(480px) {
            margin-top: remy(60);
            .not-home & { width: remy(225); }
        }

        @include break(768px) {         
            display: inline-block;
            margin-left: remy(60);
            margin-top: 0;

            &:first-child { margin-left: 0; }
            .front-page & {
                width: remy(230);
            }

            .not-home & {
                width: auto;
                margin: 0 remy(24);
            }
        }

        @include break(992px) {
            width: remy(230);
            .not-home & {
                width: auto;
                margin: 0 remy(24);
            }
        }

        .post-img {
            display: block;
            overflow: hidden;
        }
        img {
            border-radius: remy(7) remy(7) 0 0;
            width: 100%;
            @include transition($fade);
            &:hover {
                @include transform(scale(1.2));
            }
        }

        .post-logo {
            @include position(absolute, $top: 0, $right: 0);
            z-index: 2;
            img {
                &:hover { @include transform(scale(1)); }
            }
        }

        .entry-meta {
            background: $color-white;
            padding: 0 remy(20);
            @include font(11, 24, $font-family: $font-semi-bold);
            z-index: 999;

            .category {
                position: relative;
                padding-right: remy(5);
                margin-right: remy(6);              
                &::after {
                    @include position(absolute, $top: 60%, $right: 0);
                    @include transform(translateY(-60%));
                    content: '';
                    width: 1px;
                    height: 70%;
                    background: $color-grey;
                }
                a {
                    color: $color-main;
                    font-family: $font-semi-bold;
                    &:hover { color: $color-blue; }
                }
            }
            .entry-date {
                font-family: $font-semi-light;
            }
        }
        .entry-title {
            @include font(16, 20, bold, $color-white, $font-family: $font-semi-light);
            padding: remy(16) remy(20) 0;

            @include break(992px) { font-size: remy(17); }
            a { color: $color-white; }
        }
        .entry-content {
            padding: remy(2) remy(20) 0;
            z-index: 999;
            p {
                @include font(13, 17, normal, $color-white);
                text-align: justify;
            }
        }
        .entry-header { z-index: 999; }
        .btn-secondary {
            border: 1px solid $color-white;
            font-size: remy(12);
            line-height: remy(18);
            background: none;
            position: relative;
            z-index: 4;
            &:hover {
                background: $color-white;
                color: $color-blue-dark;
            }
        }
    }

    .not-home & {

        .slider-disabled {
            @include break(768px) {
                display: inline-block;
                article { width: 14.0625rem; }
            }
            @include break(992px) {
                article { width: 17.1875rem; }
            }
            @include break(1200px) {                
                article {
                    width: remy(230);
                    margin: 0 1.5rem;
                }
            }
        }


        .owl-slider {
            margin-top: remy(30);
            cursor: grab;

            @include break(480px) { margin-top: remy(50); }
            @include break(992px) { margin-top: remy(66); }
            @include break(1200px) {
                margin-top: remy(58);

            }
        }


        .owl-carousel {
            .owl-nav {
                [class*=owl-] {
                    @include position(absolute, $top: 50%);
                    @include transform(translateY(-50%));
                    text-indent: -9999px;
                    height: remy(28);
                    width: remy(15);
                }
                .owl-next {                 
                    background: path('news-slider-next.png') no-repeat !important;
                    right: 0;
                    @include break(576px) { right: remy(10); }
                    @include break(768px) { right: remy(-30); }
                    @include break(992px) { right: remy(-60); }
                }

                .owl-prev {
                    background: path('news-slider-prev.png') no-repeat !important;
                    left: 0;
                    @include break(576px) { left: remy(10); }
                    @include break(768px) { left: remy(-30); }
                    @include break(992px) { left: remy(-60); }
                }
            }
        }
    }

    .archive-page & {
        .slider-disabled {
            margin-top: remy(50);
        }
    }
}

