.processbar {
    position: relative;
    &::before {
        @include break(768px) {
            @include position(absolute, $top: remy(236), $left: 0px);
            @include background-gradient($color-green-light, $color-blue-light, horizontal);
            content: '';
            width: 67%;
            height: remy(3);
        }

        @include break(992px) {
            top: remy(202);
            width: 66%;
        }

        @include break(1200px) {
            top: remy(282);
            width: 50%;
        }

    }
}

.process {  
    position: relative;
    top: 20px;
    // top: remy(-20);

    @include break(480px) {
        padding-top: remy(20);
        // top: remy(-20);
        top: remy(20);
    }

    @include break(768px) { margin-bottom: remy(63); }

    @include break(992px) {
        padding-top: 0;
        // top: remy(-20);
        margin-bottom: 0;
        top: remy(20);
    }

    @include break(1200px) {
        padding-top: 0;
        top: 0;
        margin-top: remy(100);
    }

    h2 {
        text-align: center;
    }
    &-block {       
        margin-top: remy(45);
        position: relative;
        text-align: left;
        @include break(992px) {
            display: flex;
            text-align: center;
        }
        &::before {
            @include position(absolute, $top: remy(20), $left: remy(45));           
            @include background-gradient($color-green-light, $color-blue-light, horizontal);
            
            height: 19.8125rem;
            width: remy(3);
            content: '';
            

            @include break(347px) { height: remy(320); }

            @include break(480px) {
                left: remy(70);
                top: remy(25);
                height: remy(430);
            }

            @include break(768px) {
                @include position(absolute, $top: 6.0625rem, $left: 0);
                height: remy(3);
                width: remy(580);
            }
            @include break(992px) { width: remy(732); }
            @include break(1200px) { width: remy(840); }
        }
        li {
            display: flex;
            flex-wrap: wrap;
            position: relative;
            width: 100%;
            margin-bottom: remy(40);

            // @include break(768px) { width: 70%; }

            @include break(768px) {
                display: inline-block;
                width: 6.8125rem;
                text-align: center;
                margin-right: remy(10);
                margin-bottom: 0;
                &:first-child { margin-left: 0; }
            }
            @include break(992px) {
                width: remy(125);
                &:first-child { margin-left: 6rem; }
            }
            @include break(1200px) { &:first-child { margin-left: remy(205); } }

            &::before {
                @include border-radius(100%);
                @include position(absolute, $top: 50%, $left: 0);
                @include transform(translateY(-50%));
                content: '';
                width: remy(13);
                height: remy(13);
                background: $color-line;                
                @include break(480px) {
                    width: remy(20);
                    height: remy(20);
                    left: remy(22);
                }
                @include break(768px) {                    
                    @include transform(translate(-50%, 0));
                    left: 50%;         
                    top: 5.5625rem;           
                }
            }
            &:first-child {
                &::before {
                    @include background-gradient($color-green-light, $color-blue-light, horizontal);
                }
            }
            p {
                @include font(16, 18, $font-family: $font-semi-light);
                margin-bottom: 0;
                flex: 0 0 52%;
                max-width: 52%;
                align-items: center;
                display: flex;

                @include break(480px) {
                    @include font(16 ,24);
                    flex: 0 0 54%;
                    max-width: 54%;
                }
                @include break(576px) {
                    flex: 0 0 70%;
                }
                @include break(768px) {
                    margin-bottom: 1rem;
                    font-size: remy(18);
                    line-height: remy(18);
                    flex: 0 0 100%;
                    max-width: 100%;
                    justify-content: center;
                }
            }           
        }
    }
    .icon-block {
        width: remy(40);
        height: remy(40);
        box-shadow: 0 0 30px rgba($color-black, .1);
        display: flex;
        float: left;
        align-items: center;
        justify-content: center;
        border-radius: remy(5);
        margin-left: remy(30);
        margin-right: remy(20);     
        flex: 0 0 remy(40);
        img { width: 52%; }     
        @include break(480px) {
            width: remy(65);
            height: remy(65);
            margin-left: remy(60);
            margin-right: remy(35);
            flex: 0 0 remy(65);
            img { width: auto; }
        }       
        @include break(768px) {
            margin: auto;
            margin-bottom: remy(62);
            float: none;
        }
    }
}

.form-block {
    background: $color-blue-darkest;
    h3 {
        color: $color-blue-light;
        margin-bottom: remy(15);
        @include break(480px) { margin-bottom: remy(30); }
        @include break(992px) {
            margin-bottom: remy(35);
            font-size: 2rem;
        }
    }

    label {
        color: $color-white;
        margin-bottom: 0;
        small { color: $color-required; }
        font-size: remy(14);
        @include break(992px) {
            font-size: remy(18);
        }
    }

    .form-control {
        background: none;
        border: none;
        border-bottom: 2px solid $color-line-form;
        border-radius: 0;
        box-shadow: none;
        outline: none;
        color: $color-white;
        padding: 0;
        resize: none;
        line-height: 0.5rem;
        margin-bottom: remy(20);
        @include break(480px) {
            line-height: 1rem;
            margin-bottom: remy(30);
        }
        @include break(992px) {
            line-height: 1.5rem;
            margin-bottom: remy(40);
        }
    }
    textarea {
        background: path('textarea-bg.png') no-repeat bottom right !important;
        line-height: 1rem !important;
        height: remy(90);
        @include break(768px) { height: remy(50); }
        @include break(992px) {
            line-height: 1.5rem !important;
            height: auto;
        }

    }

    .form-check {       
        @include break(768px) {
            margin-right: remy(45);
            margin-top: remy(5);
            float: left;
        }
        .helper {
            &::before {
                border-color: $color-white;
            }
            &::after {
                width: remy(16);
                height: remy(15);
                top: remy(-3);
                left: remy(1);
                @include break(480px) {
                    left: remy(8);
                    top: remy(1);
                }
            }
        }
        input[type="checkbox"]:checked ~ .helper::after {
            background: path('check-tick-small.png') no-repeat;
            @include break(480px) {
                background: path('check-tick.png') no-repeat;
            }
        }
    }
    .btn {
        float: left;
        border: 1px solid $color-blue-dark;
        &:hover {
            background: $color-white;
            color: $color-blue-dark;
        }
        &:focus {
            border: 1px solid $color-blue-dark;
        }
    }

    .wpcf7-form {
        &.invalid {
            .form-control {
                margin-bottom: remy(5);
            }
            span.wpcf7-not-valid-tip {
                @include font(14, 14, $color: $red);
                .popup-block & { font-size: 0.66rem; }
                margin-bottom: remy(20);
                @include break(480px) { margin-bottom: remy(30); }
                @include break(992px) { margin-bottom: remy(40); }
            }
            .wpcf7-validation-errors {
                color: $color-white;
                padding-bottom: remy(7);
                .popup-block & {
                    color: $color-main;
                    font-size: 0.71rem;
                    margin-left: 0;
                    margin-right: 0;
                    margin-bottom: 0;
                    padding-bottom: remy(5);
                }
            }
        }
        &.sent {
            .wpcf7-response-output {
                margin-left: 0;
                margin-right: 0;
                margin-bottom: 0;
                @include font(15);
                padding-bottom: remy(5);
            }
        }
    }
}


#pfcf-popup a.close { z-index: 9999 !important; }

.cc-banner {
    @include position(fixed, $right: 0, $left: 0, $bottom: 0);
    @include font(16, $color: $color-white);
    background: $color-blue-darkest;
    align-items: center;
    padding: 1em 1.8em;
    width: 100%;
    flex-direction: row;
    overflow: hidden;
    box-sizing: border-box;
    display: flex;
    flex-wrap: no-wrap;
    z-index: 999;
    opacity: 1;
    transition: opacity 1s ease;
    @media screen and (max-width: 414px) and (orientation: portrait), screen and (max-width: 767px) and (orientation: landscape) {
        align-items: unset;
        flex-direction: column;
        left: 0;
        right: 0;
        bottom: 0;
    }
    .cc-message {
        flex: 1;
        @media screen and (max-width: 414px) and (orientation: portrait), screen and (max-width: 767px) and (orientation: landscape) {
            margin-bottom: 1em;
        }
    }
    .cc-link {
        color: $color-white;
        opacity: .8;
        display: inline-block;
        padding: .2em;
        text-decoration: underline;
        &:visited, &:active { color: $color-white; }
        &:hover {
            opacity: 1;
        }
    }
    .cc-compliance {
        display: flex;
        align-items: center;
        align-content: space-between;
        @media screen and (max-width: 414px) and (orientation: portrait), screen and (max-width: 767px) and (orientation: landscape) {
            flex: 1;
        }
    }
    .cc-btn {
        display: block;
        padding: .4em .8em;
        font-size: .9em;
        font-weight: 700;
        text-align: center;
        white-space: nowrap;
        touch-action: manipulation;
        flex: 1;
        min-width: 140px;
        @include background-gradient($color-blue-light, $color-green-light, horizontal);
        color: $color-white;

        &:hover { @include background-gradient($color-blue-light, $color-green-light, horizontal); }
    }
}