//ARTIFICIAL INTELLIGENCE
.txt-container {
    @include position(relative, $top: remy(-130), $left: 9.75%);
    background: $color-white;
    padding: remy(80) remy(10) remy(22) remy(32);
    width: 100%;
    box-shadow: 0 0 100px rgba($color-black, .4);
    z-index: 1;

    .about-img {
        position: relative;
        left: remy(-48);
        margin-bottom: remy(20);
    }

    img {
        .news-detail & { margin-bottom: 20px; }
    }


    @include break(480px) {
        top: remy(-198);
        padding-top: remy(120);
        padding-right: 1rem;
    }
    @include break(576px) {
        width: 200%;
        .about-img { img { max-width: remy(450); } }
    }
    @include break(768px) {
        top: remy(-100);
        .about-img {
            left: -7rem;
        }
        .about-img { img { max-width: 100%; } }
    }
    @include break(992px) {
        top: remy(-150);
        padding: remy(120) remy(80) remy(75);
    }
    @include break(1200px) {
        top: remy(-160);
        left: remy(72);
        padding: remy(90) remy(100) remy(75);

        &.aboutpage {
            left: remy(190);
            min-height: remy(550);
        }

        .about-img {            
            left: remy(-80);
            top: remy(60);      
            display: inline-block;
            position: absolute;
        }

        .about-con {
            width: 50%;
            display: inline-block;
            position: relative;
            left: remy(420);            
            p {
                font-size: remy(16);
                line-height: remy(23);
            }
        }
    }

    .about-con {
        .btn {
            @include border-radius(8px);
            @include background-gradient($color-green-light, $color-blue-light, horizontal);
            padding: remy(8) remy(15);          

            i {
                width: remy(22);
                height: remy(22);
                background: path('btn-icon.png') no-repeat;
                display: inline-block;
                float: left;
                margin-right: remy(10);
                @include break(768px) { margin-top: remy(2); }
            }
            span { display: inline-block; }
        }
    }

    .jobs & {
        .btn {
            @include border-radius(0 8px 8px 0);
            transition: all ease .5s;
            position: relative;
            padding: remy(8) remy(15);      
            padding-left: remy(57);     
            margin-left: remy(-32);     
            &:hover {
                @include background-gradient($color-green-light, $color-blue-light, horizontal);
            }

            @include break(768px) {
                padding-left: remy(116);
                margin-left: remy(-70);
            }

            i {
                width: remy(9);
                height: remy(15);
                background: path('job_detail_btn_icon.png') no-repeat;
                display: inline-block;
                margin-left: remy(10);
                float: left;
                @include break(480px) { margin-top: remy(4); }
            }
            strong {
                display: inline-block;
                float: left;
                font-weight: normal;

            }
        }
    }

    .not-home & {       
        top: remy(-126);
        padding-top: remy(30);
        padding-bottom: remy(30);
        padding-right: remy(20);
        @include break(480px) {
            padding-right: remy(30);
            top: remy(-200);
        }
        @include break(576px) { top: remy(-225); }
        @include break(768px) {
            top: remy(-140);
            padding-top: remy(60);
            padding-bottom: remy(30);
            padding-left: remy(70);
            padding-right: remy(10);
        }       
        .ai-con {
            h5 {
                color: $color-blue-lightest;
                font-weight: normal;
            }
        }
    }


    .ai-con {
        @include break(576px) {
            width: remy(440);
            .not-home & { width: 26rem; }
        }
        @include break(768px) {
            width: remy(575);
            .not-home & { width: 32rem; }
        }       
        @include break(992px) {
            width: 47rem;
            .not-home & { width: 45rem; }
        }
        @include break(1200px) {
            width: remy(930);
            .not-home & { width: 60rem; }
        }
    }

    &.aboutpage {
        .ai-con {
            @include break(1200px) { width: remy(872); }
        }
    }

    h1 {
        margin-bottom: remy(5);
        @include break(480px) {
            margin-bottom: remy(33);
            .not-home & {
                margin-bottom: remy(18);
            }
        }
    }

    p { font-family: $font-semi-light; }

    .tab-content {
        width: 100%;
        max-width: remy(650);
        .btn {
            font-size: 14px;
            padding-bottom: 2px;
            @include break(992px) {
                padding: 0;
                padding-bottom: 2px;           
            }
        }
    }
    .nav {
        text-align: center;
        li {
            padding-bottom: remy(2);
            margin-bottom: remy(13);
            position: relative;
            width: 50%;

            @include break(480px) {
                margin-bottom: remy(23);
            }

            @include break(768px) { width: 25%; }

            @include break(992px) {             
                width: auto;
                margin-right: remy(45);
                margin-left: remy(30);

                &::after {
                    @include position(absolute, $top: 40%, $right: remy(-38));
                    @include transform(translateY(-40%));
                    content: '';                    
                    height: 86%;
                    width: 1px;
                    background: $color-light-grey;
                }
            }

            a {
                display: inline-block;
                border-bottom: 2px solid $color-white;
                outline: none;                
            }

            &:first-child { margin-left: 0; }
            .active {
                border-color: $color-blue-light;
            }           
            &:last-child {
                &::after {
                    background: none;
                }
            }
        }       
        .img-block {
            display: flex;
            align-items: flex-end;
            justify-content: center;
            height: remy(80);

            @include break(480px) { height: remy(95); }

            img {
                height: remy(60);
                @include break(480px) { height: auto; }
            }
        }
        figure {
            margin-bottom: remy(10);
            @include break(480px) { margin-bottom: 1rem; }
        }
        figcaption {
            @include font(16, $font-family: $font-semi-light);
            margin-top: remy(13);
            @include break(480px) {
                @include font(22, $font-family: $font-semi-light);
                margin-top: remy(22);
            }
            @include break(768px) {
                padding-left: remy(10);
                padding-right: remy(10);
            }
        }
    }
}