.author {
    &-header {
        width: 100%;
        margin-bottom: remy(20);
        &::after {
            content: '';
            display: block;
            clear: both;
        }
        img {
            float: left;
            margin-right: remy(20);
            margin-bottom: remy(20);
        }
        dl {
            @include font;
            dd { margin-left: 0; }
        }
    }
    h3 { width: 100%; }

    &-postList {
        margin: 0;
        padding: 0;
        list-style: none;
        li {
            position: relative;
            padding-left: remy(15);
            margin-bottom: remy(5);
            &::before {
                content: '-';
                @include position(absolute, $top: 0, $left: 0);
            }
        }
    }
}