.contact-info { 
    [class*=col-] {
        margin-bottom: remy(20);
        &:last-child { margin-bottom: 0; }
    }

    address {
        margin: 0;
    }

    // .icon-block, .info-block {
    //  float: left;
    // }

    .flex-box {
        display: flex;
        flex-wrap: wrap;
    }

    .icon-block {       
        margin-left: 20%;
        flex: 0 0 20%;
        max-width: 20%;
        align-items: center;
        @include break(480px) {
            max-width: 17%;
            flex: 0 0 17%;
        }
        @include break(768px) {
            margin-left: 10%;
            max-width: 20%;
            flex: 0 0 20%;
        }
        @include break(992px) {
            margin-left: 0;
        }
        img { margin-top: remy(30); }
        &.envelope img { font-size: remy(30); }
        &.phone img { margin-top: remy(20); }
    }

    .info-block {
        max-width: 60%;
        flex: 0 0 60%;
        line-height: remy(24);
        @include break(768px) {
            max-width: 70%;
            flex: 0 0 70%;
        }
        @include break(992px) {
            max-width: 80%;
            flex: 0 0 80%;
        }
        h6 {
            margin-bottom: remy(5);
            font-family: $font-semi-bold;
            font-weight: 600;
        }       
        a {
            color: $color-main;
            &:hover { color: $color-blue; }
        }
    }
}

#wrapper-footer {
    position: relative;
    background: $color-white-off2;
       
    &.no-mar { margin-top: remy(-67); }
    &.news-footer { margin-top: remy(-122); }
    &.news-detail-footer { margin-top: remy(50); }
    &.services-footer { margin-top: remy(-13); }    
    @include break(480px) {
        &.no-mar { margin-top: remy(-110); }
        &.news-footer { margin-top: remy(-147); }
        &.news-detail-footer { margin-top: remy(60); }
        &.services-footer { margin-top: remy(-70); }
    }
    @include break(576px) {
        &.no-mar { margin-top: remy(-122); }
        &.news-footer { margin-top: remy(-200); }
        &.news-detail-footer { margin-top: remy(60); }
        &.services-footer { margin-top: remy(-100); }
    }
    @include break(768px) {        
        &.no-mar { margin-top: remy(-47); }
        &.news-footer { margin-top: remy(-135); }
        &.news-detail-footer { margin-top: remy(65); }
        &.services-footer { margin-top: remy(-45); }
    }
    @include break(992px) {        
        &.no-mar { margin-top: remy(-25); }
        &.news-footer { margin-top: remy(-115); }
        &.news-detail-footer { margin-top: remy(75); }
    }
    @include break(1200px) {        
        &.no-mar { margin-top: remy(-30); }
        &.news-footer { margin-top: remy(-100); }
        &.news-detail-footer { margin-top: remy(80); }        
        &.services-footer { margin-top: remy(-75); }
    }
}

.footer-block {
    top: remy(-20);
    position: relative;      
    @include break(1200px) { top: 0; }
}

.container-foo {    
    width: 100%;
    margin: auto;   
    position: relative;

    
    .contact-info {
        padding-top: remy(15);
        position: relative;

        @include break(992px) { padding-top: 0; }
        @include break(1200px) { padding-left: 6.5625rem; }
    }
    .form-block {
        border-radius: remy(5) 0 0;             
        margin-left: remy(40);
        margin-top: remy(27);
        position: relative;
        padding: remy(30) remy(40);
        width: 80%;
        z-index: 1;
        
        @include break(480px) {
            padding: remy(50) remy(60);
            margin-left: 3.5rem;
        }

        @include break(576px) { margin-left: remy(64); }

        @include break(768px) {
            padding: remy(50);
            margin-top: 0;
            width: 90%;
            margin-left: 0;
        }

        @include break(992px) {
            padding: remy(55) 4.9375rem;
            width: auto;
            margin-left: remy(100);
        }

        @include break(1200px) { padding: remy(55) remy(95); }

        .btn {
            margin-top: remy(20);
            @include break(768px) { margin-top: remy(5); }
            @include break(992px) { margin-top: 0; }
        }

        .wpcf7-form {
            &.sent {
                .wpcf7-response-output { color: $color-white; }
            }
        }       
    }
}

.site-footer {
    @include background-gradient($color-blue-light, $color-blue-dark, horizontal);
    padding-bottom: remy(10);
    padding-top: remy(35);
    margin-top: remy(-36);
    position: relative;
    @include break(480px) {
        margin-top: remy(-56);
        padding-top: remy(65);
        padding-bottom: remy(20);
    }
    @include break(992px) {
        margin-top: remy(-50);
        padding-top: remy(90);
        padding-bottom: remy(30);
    }
    @include break(1200px) {
        margin-top: remy(-50);
        padding-top: remy(110);
        padding-bottom: remy(30);   
    }
    .privacy {
        text-align: center;
        
        @include break(992px) {
            text-align: left;
        }

        ul {
            padding-left: 0;

            li {
                display: inline-block;              
                position: relative;
                padding-left: remy(12);
                margin-left: remy(7);
                &::after {
                    @include position(absolute, $top: 50%, $left: 0);
                    @include transform(translateY(-50%));
                    content: '';
                    width: 1px;
                    height: remy(15);
                    background: $color-white;
                }
                &:first-child {
                    padding-left: 0;
                    margin-left: 0;
                    &::after { background: none; }                  
                }
                a {
                    @include font(14, 14, $font-family: $font-semi-light, $color: $color-white);
                    @include break(480px) {
                        font-size: remy(16);
                        line-height: remy(16);
                    }
                    &:hover {
                        // color: $color-blue-darkest;
                        text-decoration: underline;
                    }
                }
            }
        }
    }
    .social {
        margin-top: remy(12);
        margin-bottom: remy(8);
        @include break(480px) {
            margin-bottom: remy(20);
            margin-top: remy(25);
        }
        @include break(768px) {
            margin-top: 0;
            margin-bottom: 0;
        }
        ul {
            padding: 0;
            text-align: center;
            li {
                display: inline-block;
                margin-left: remy(8);
                box-shadow: 0 0 20px rgba($color-black, .2);

                @include break(480px) { margin-left: remy(16); }

                @include break(768px) { margin-left: remy(8); }

                @include break(992px) { margin-left: remy(10); }

                @include break(1200px) { margin-left: remy(16); }

                &:first-child { margin-left: 0; }

                a {
                    height: remy(22);
                    width: remy(22);
                    background: $color-white;                   
                    font-size: remy(14);
                    display: flex;
                    color: $color-blue-dark;
                    align-items: center;
                    justify-content: center;
                    border-radius: 5px;
                    &:hover {
                        color: $color-blue-darkest;
                    }

                    @include break(480px) {
                        height: remy(35);
                        width: remy(35);
                        font-size: remy(20);
                    }

                    @include break(768px) {
                        height: remy(22);
                        width: remy(22);
                        font-size: remy(14);
                    }

                    @include break(992px) {
                        height: remy(35);
                        width: remy(35);
                        font-size: remy(20);
                    }
                }
            }
        }
    }

    .foo-logo {
        text-align: right;
        a {
            @include font(16, $color: $color-white, $font-weight: bold);            
            @include break(480px) { font-size: remy(22); }
        }
    }

    .site-info {        
        p {
            @include font(12, $color: $color-white);            
            margin-bottom: 0;
            margin-top: remy(2);
            padding-left: remy(5);
            @include break(480px, 992px) { font-size: 1rem; }
            @include break(768px) {
                margin-top: remy(20);
                padding-left: 0;
                text-align: center;
            }
            @include break(992px) {
                margin-top: remy(35);
            }
        }
    }
}

