#single-wrapper .post {
    .entry-content { margin-top: remy(20); }
    .entry-meta { margin-bottom: remy(40); }
}

#index-wrapper {
    .site-main {
        display: flex;
        flex-wrap: wrap;
        width: 100%;
    }
}

.post-navigation { margin-top: remy(20); }

.wrapper{
    .error404 & {
        #searchform {
            background: $color-light-grey;
            padding: 1.875rem;
            margin-top: 2.875rem;
        }
    }
    .page-title {
        @include font(32);
        margin-bottom: 1.875rem;
    }
    #right-sidebar {        
        margin-top: 3.125rem;
        @include break(768px) { margin-top: 0; }
    }

    &#wrapper-footer {
        .container-fluid { max-width: 100% !important; }
    }
    
    .pagination { margin-top: 1.875rem; }

    article {
        &::after {
            clear: both;
            content: '';
            display: block;
        }
    }

    .blog-post {
        width: 100%;
        @include break(768px) {
            -webkit-box-flex: 0;
            -ms-flex: 0 0 48%;
            flex: 0 0 48%;
            max-width: 48%;
        }
        // display: inline-block;
        text-align: center;
        margin-bottom: 1.875rem;

        &:last-child { margin-bottom: 0; }
        &:nth-child(odd) { @include break(768px) { margin-right: 2%; } }
        &:nth-child(even) { @include break(768px) { margin-left: 2%; } }
        .wp-post-image { margin-bottom: remy(23); width: 100%; }
        .entry-content p {
            position: relative;
            word-wrap: break-word;
            overflow: hidden;
            max-height: 3rem;
        }

        .entry-header {
            padding: 0 1.875rem;
            .entry-title {
                @include font(20, $color: $color-blue, $font-family: $font-heading);
                a { @include font(20, $color: $color-blue, $font-family: $font-heading); }
            }
        }
        .entry-meta {
            margin-bottom: remy(20);
            .entry-date, .byline, .comments-link a { color: $color-grey; }
            .byline a { color: $color-blue; }
            .byline, .comments-link { margin-left: remy(35); }
            .comments-link i { margin-right: remy(5); }
        }
        .entry-content {
            padding: 0 1.875rem;
        }
        .entry-footer {
            padding: 0 1.875rem;
            .comments-link a { color: $color-purple; }
        }
        .entry-summary { padding: 0 1.875rem; }
    }

    .entry-footer { padding-top: 1.875rem; }

    .entry-content {
        &::after, p::after {
            content: '';
            clear: both;
            display: block;
        }
    }

    .widget {
        background: $color-light-grey;
        padding: 1.875rem;
        margin-bottom: 1.875rem;
        border-bottom: 1px solid $color-shadow;

        ul { padding: 0; }

        &.widget_recent_entries { margin-top: 1.875rem; }
        
        li {
            margin-bottom: .625rem;
            padding-bottom: .625rem;
            border-bottom: 1px solid $color-shadow;
            font-size: .875rem;

            &:last-child {
                margin-bottom: 0;
                padding-bottom: 0;
                border-bottom: none;
            }

            a:hover {
                color: $color-purple;
            }
        }       
    }

    .assistive-text, .widget-title, .widgettitle {
        @include font(20.8, $font-weight: 500);
        border-bottom: 2px solid $color-shadow;
        display: block;
        position: relative;
        padding-bottom: .625rem;
        margin-bottom: 1.25rem;
        line-height: 1.75rem;
        &:after {
            content: '';
            @include position(absolute, $bottom: -.125rem, $left: 0);
            background: $color-grey;
            height: .125rem;
            width: 1.75rem;
        }
    }

    .comments-area {
        margin-top: 4rem;
        .comment {
            padding: 1.875rem;
            margin-bottom: 1.875rem;
            @include box-shadow(0, 0, 5px 0, $color-shadow);
            border-bottom: 1px solid $color-shadow;

            article { margin-bottom: 0; }

            .children {
                padding-left: .625rem;
                list-style: none;
                @include break(768px) {
                    padding-left: 2.5rem;
                }
                .comment {
                    padding: 1rem 0;
                    @include break(768px) { padding: 1.875rem 0; }
                    margin-bottom: 0;
                    box-shadow: none;
                    border-bottom: none;
                    margin-left: .625rem;
                }
            }
        }

        .pingback { margin-bottom: 1rem; }
        .comments-title {
            font-size: 1rem;
            margin-bottom: 1.875rem;
            @include break(768px) {
                font-size: 1.5rem;
            }
            span {
                display: inline-block;
                font-size: 1rem;
                @include break(768px) { font-size: 1.5rem; }
            }
        }

        .comment-list {
            padding: 0;
            list-style: none;
            article {
                box-shadow: none;
                border: none;
            }
        }

        .comment-respond {
            padding: 1.875rem;
            @include box-shadow(0, 0, 5px 0, $color-shadow);
            border-bottom: 1px solid $color-shadow;
            margin-top: 1.875rem;

            .comment-reply-title small {
                @include font;
                float: right;
                margin-top: .625rem;
                a { color: $color-red; }
            }

            .comment-form {
                .comment-form-comment {
                    margin-top: remy(50);
                }
                .comment-form-author,
                .comment-form-email,
                .comment-form-url {
                    @include break(768px) {
                        width: 30%;
                        display: inline-block;
                    }
                }
                .comment-form-email,
                .comment-form-url {
                    @include break(768px) { margin-left: 4.6%; }
                }
            }
        }

        .comment-body {
            .says { display: none; }
            .avatar {
                width: 4.375rem;
                height: 4.375rem;
                float: left;
                margin-right: 1.25rem;
            }
            .fn {
                @include font(14, $font-weight: 600, $text-transform: uppercase);
                margin-bottom: .3125rem;
                display: inline-block;
                a { color: $color-main; }
            }
            .comment-metadata { @include font(14, $text-transform: uppercase) }
            .comment-content {
                margin-top: 1.25rem;                
                @include break(768px) {
                    margin-left: 5.625rem;
                    float: left;
                    width: 75%;
                    margin-top: 0;
                }
                @include break(992px) { width: 86%; }
                p { @include font(14 ); }
            }
            .reply {
                @include break(768px) { margin-left: 5.625rem; }
                .comment-reply-link {
                    @extend .btn-small;
                    background: $color-blue;
                    color: $color-white;
                    padding: .3125rem 1.25rem;
                }
            }
        }       
    }
}

.recent-post {
    margin-top: remy(50);
    padding-top: remy(50);
    border-top: 1px solid $color-light-grey;
    h3 {
        color: $color-blue;
        margin-bottom: remy(50);
    }
    .blog-post {
        float: left;
        img {
            margin-bottom: remy(20);
        }
    }
    &::after {
        content: '';
        clear: both;
        display: block;
    }
}

.blog-detail {
    margin-top: remy(-40);
    margin-bottom: remy(40);
    position: relative;
    text-align: center;
    font-size: remy(22);
    a {
        color: $color-white;        
    }
    .comments-link {        
        color: $color-grey;
    }
    .byline {       
        color: $color-white;
        margin: 0 remy(20);
    }
}

.archive-block, .search .site-main {
    width: 100%;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-wrap: wrap;
    flex-wrap: wrap;
}

.search-post {
    margin-top: remy(40);
    &:first-child { margin-top: 0; }
    .entry-title {
        a {
            color: $color-main;
            &:hover {
                color: $color-blue;
            }
        }
    }
    .comments-link { display: none; }
    .btn {
        border: none;
        &:hover {
            @include background-gradient($color-green-light, $color-blue-light, horizontal);
        }
    }
}

.feat-img { margin-bottom: remy(30); }