.sr-only {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  border: 0; }

.sr-only-focusable:active, .sr-only-focusable:focus {
  position: static;
  width: auto;
  height: auto;
  margin: 0;
  overflow: visible;
  clip: auto; }

.gallery {
  margin-bottom: 1.5em; }

.gallery-item {
  display: inline-block;
  text-align: center;
  vertical-align: top;
  width: 100%; }
  .gallery-columns-2 .gallery-item {
    max-width: 50%; }
  .gallery-columns-3 .gallery-item {
    max-width: 33.33%; }
  .gallery-columns-4 .gallery-item {
    max-width: 25%; }
  .gallery-columns-5 .gallery-item {
    max-width: 20%; }
  .gallery-columns-6 .gallery-item {
    max-width: 16.66%; }
  .gallery-columns-7 .gallery-item {
    max-width: 14.28%; }
  .gallery-columns-8 .gallery-item {
    max-width: 12.5%; }
  .gallery-columns-9 .gallery-item {
    max-width: 11.11%; }

.gallery-caption {
  display: block; }

html {
  font-size: 100%;
  overflow-x: hidden; }

body {
  font-size: 1rem;
  font-family: "weblysleek_uilight", sans-serif;
  line-height: 1.125rem; }
  @media (min-width: 0) and (max-width: 1199px) {
    body {
      overflow-x: hidden !important;
      width: 100%; } }

article, aside, dialog, figcaption, figure, footer, header, hgroup, main, nav, section {
  z-index: 0; }

* {
  margin: 0;
  box-sizing: border-box;
  overflow-wrap: break-word; }

p {
  font-family: "weblysleek_uilight", sans-serif;
  font-size: 0.875rem;
  line-height: 1.375rem;
  font-weight: normal;
  color: #3d545c;
  text-transform: none;
  font-style: normal; }

ul, ol {
  margin: 0;
  font-family: "weblysleek_uilight", sans-serif;
  font-size: 0.875rem;
  line-height: 1.375rem;
  font-weight: normal;
  color: #3d545c;
  text-transform: none;
  font-style: normal; }

a {
