//OUR-VISION
.mobile-col {
    @include break(0, 767px) {
        padding-left: 0;
        padding-right: 0;
    }
}
.vision {   
    position: relative; 
    padding-bottom: remy(57);
    .not-home & { margin-top: remy(30); }
    @include break(480px) { .not-home & { margin-top: remy(55); } }
    @include break(576px) { .not-home & { margin-top: remy(41); } } 
    @include break(992px) {
        .not-home & { margin-top: remy(50); }
        padding-bottom: 0;
    }

    @include break(1200px) {
        .not-home & { margin-top: remy(100); }
    }

    img {
        border-radius: 0 remy(8) 0 0;
        width: 90%;
        @include break(992px) { width: auto; }
        .about-page & {
            @include break(0, 768px) { width: 100%; }
        }
    }
    &-tags {
        @include position(relative, $top: remy(-20), $left: 0);
        border-radius: remy(5);
        width: 100%;   
        img { width: 100%; }     
        @include break(480px) {            
            top: remy(-10);
            width: 76.5%;
        }
        @include break(480px, 767px) { margin: auto; }
        @include break(768px) {
            width: 34%;
            top: -28rem;
            left: remy(37);
        }
        @include break(992px) {
            top: -33.25rem;
            left: 6.8125rem;
            width: remy(350);
        }
        @include break(1200px) {
            position: absolute;
            top: remy(354);
            left: remy(157);
        }
        span {
            @include font(16, 24, $color: $color-white);
            display: inline-block;
            padding: remy(8);
            
            &.small {
                font-size: remy(13);
                line-height: remy(20);
                padding: 0 remy(13);
            }

            @include break(480px) {
                font-size: remy(20);
                line-height: remy(28);
                padding: remy(10) remy(13);
            }

            @include break(992px) {
                font-size: remy(20);
                line-height: remy(28);
                padding: remy(15);
                &.small {
                    font-size: remy(16);
                    line-height: remy(22);
                    padding: 0 remy(15);
                }
            }
        }
    }
    &-text {
        @include position(relative, $top: -1.125rem, $left: remy(50));
        background: path('icon-17-mobile.png') no-repeat 80% bottom $color-white;
        box-shadow: 0 0 50px rgba($color-black, .2);
        border-radius: remy(5);
        padding: remy(22) remy(30) remy(60);
        width: 76%;
        text-align: justify;
        @include break(480px) {
            left: remy(80);
            top: -1.125rem;
        }
        @include break(576px) { width: 78%; }
        @include break(768px) {
            top: -13rem;
            left: 20rem;
            width: 48%;
        }
        @include break(992px) {
            left: 33rem;
            top: -20rem;
            width: remy(380);
            padding: remy(45) remy(50) remy(80);
            background: path('icon-17.png') no-repeat 80% bottom $color-white;
        }
        @include break(1200px) {
            top: remy(-290);
            left: remy(653);
            .not-home & {
                top: remy(-225);
            }
        }
        h2 {
            margin-bottom: remy(7);
            @include break(992px) { margin-bottom: remy(20); }
        }
        // p {
        //  font-size: remy(12);
        //  @include break(992px) {
        //      @include font;
        //  }
        // }
    }
    
    &.vision-small { // class vision--active added in body through jquery
        .vision-mobile {
            @include break(0, 767px) {
                background: $color-white;
                box-shadow: 0 0 50px rgba($color-black, .2);
                width: 126%;
                left: remy(15);
                position: relative;
                padding-top: remy(30);

                @include break(480px) { left: remy(30); }
                @include break(576px) { left: remy(40); }

                img {
                    margin-left: -15px;
                    width: 72%;
                    @include break(480px) { width: 73%; }
                    @include break(576px) { width: 76%; }
                }

                .vision-text {
                    box-shadow: none;
                    top: 0;
                    left: 0;
                }
            }    
        }
        .vision-tags { top: 50px; }
    }    
}