.animated  {
  -webkit-animation-duration : 3s  ;
  animation-duration : 3s  ;

  -webkit-animation-delay : 500ms  ;
  animation-delay : 500ms  ;
}

.animate-out {
  -webkit-animation-delay : 0ms  ;
  animation-delay : 0ms  ;
}

.banner {
    background: path('slider-vector.png') no-repeat center left, linear-gradient(to right, $color-banner-first, $color-green-light);
    
    h4 {
        @include font(24, 30, $color: $color-white, $font-family: $font-semi-light);            
        margin-bottom: remy(10);
        margin-top: remy(12);
        letter-spacing: 1px;

        @include break(480px) {
            font-size: remy(38);
            line-height: remy(45);
        }
        @include break(768px) {
            font-size: remy(28);
            line-height: remy(36);
            margin-bottom: remy(10);
        }
        @include break(992px) {
            font-size: remy(36);
            line-height: remy(41);
            margin-bottom: remy(20);
        }
        @include break(1200px) {
            font-size: remy(41);
            line-height: remy(41);
        }
    }
    

    &.not-home {
        height: remy(237);      

        text-align: center;
        padding-top: remy(24);
        @include break(480px) {
            height: remy(370);
            padding-top: remy(44);
        }
        @include break(576px) {
            height: remy(420);
            padding-top: remy(54);
        }
        @include break(768px) {
            height: remy(315);
            padding-top: remy(50);
        }
    }

    .owl-item { display: table; }
    .owl-carousel {
        .item {
            height: remy(500);
            display: table-cell;            

            @include break(480px) { height: remy(755); }

            @include break(768px) { height: remy(500); }

            @include break(992px) { height: remy(650); }
        }

        .owl-nav {
            @include position(absolute, $top: remy(50), $left: remy(25));
            width: remy(21);

            @include break(480px) {
                left: remy(50);
                top: remy(80);
            }
            @include break(768px) {
                left: remy(32);
            }
            @include break(992px) {
                top: remy(120);
                left: remy(45);
            }
            @include break(1200px) {
                top: remy(150);
                left: remy(55);
            }
            
            [class*=owl-] {
                text-indent: -9999px;
                height: remy(9);
                width: remy(21);
                background: path('arrowButton.png') no-repeat 0 0 !important;
            }
            .owl-next {
                background: path('arrowButton.png') no-repeat 0 -12px !important;
                margin-top: remy(103) !important;
                @include break(480px) {
                    margin-top: remy(148) !important;

                }
                @include break(992px) {
                    margin-top: remy(138) !important;
                }
            }
        }
        .owl-dots {
            @include position(absolute, $top: remy(87), $left: remy(29));
            width: remy(21);

            @include break(480px) {
                left: remy(53);
                top: remy(140);
            }

            @include break(768px) {
                left: remy(35);
            }

            @include break(992px) {
                top: remy(165);
                left: remy(48);
            }

            @include break(1200px) {
                top: remy(205);
                left: remy(58);
            }
            
            .owl-dot {
                span {
                    background: rgba($color-white, 0.5) !important;
                    width: remy(10);
                    height: remy(10);
                }
                &.active {
                    span {
                        background: $color-white !important;
                    }
                }
            }
        }
    }


    &.front-page {
        .banner-content {
            @include position(absolute, $top: remy(28), $left: remy(85));
            width: 70%;
            
            @include break(480px) {
                left: remy(128);
                top: remy(48);
            }
            @include break(768px) {
                top: remy(92);
                left: remy(70);
                width: 90%;
                .owl-txt {
                    width: 80%;
                    float: left;
                }
            }
            @include break(992px) {
                top: remy(130);
                left: remy(90);
                width: 85%;

                .owl-txt {
                    width: 83%;
                    float: left;
                }
            }
            @include break(1200px) {
                top: remy(170);
                left: remy(115);
                width: 80%;

                .owl-txt {
                    width: auto;
                    float: none;
                }
            }
            
            .owl-img {
                float: left;
                display: none;
                @include break(768px) {
                    display: block;
                    width: remy(80);
                    margin-right: remy(17);
                    margin-top: remy(14);
                    img {
                        width: auto;
                        max-width: 100%;
                    }
                }
                @include break(992px) {             
                    width: remy(110);
                    margin-right: remy(20);
                    margin-top: remy(10);
                }
                @include break(1200px) {
                    width: remy(132);
                    margin-right: remy(30);
                    margin-top: 0;
                }
            }
            p {
                @include font(17, 22, $color: $color-white);
                margin-bottom: remy(10);
                display: inline;

                @include break(480px) {             
                    font-size: remy(25);
                    line-height: remy(34);
                }

                @include break(768px) {
                    font-size: remy(20);
                    line-height: remy(20);
                    display: block;
                    margin-bottom: remy(8);
                }

                @include break(992px) {
                    font-size: remy(25);
                    line-height: remy(35);
                    margin-bottom: remy(10);
                }
            }
        }
    }
}